.c-template-book-club {
  padding-block-end: 0;
  font-size: clamp(0.875rem, 0.137rem + 2.3622vw, 1.25rem);
  line-height: 1.2;
  font-family: aktiv-grotesk, -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Cantarell, Ubuntu, roboto, noto, arial, sans-serif;
}

  .c-template-book-club h1,
  .c-template-book-club h2,
  .c-template-book-club h3 {
    font-family: inherit;
    line-height: inherit;
  }
.c-book-club-header {
  --container-padding-block-end: clamp(25px, calc(25px + 15 * (100vw - 500px) / 254), 40px);

  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: clamp(30px, calc(30px + 10 * (100vw - 500px) / 254), 40px);
  font-size: clamp(1.125rem, 0.633rem + 1.5748vw, 1.375rem);
  line-height: 1.25;
}
.c-book-club-header__title-block {
    min-inline-size: 300px;
    flex: 1 2 25%;
  }
.c-book-club-header__body-block {
    flex: 2.25 1 50%;
    display: flex;
    flex-wrap: wrap;
    gap: clamp(20px, calc(20px + 20 * (100vw - 500px) / 254), 40px);
    align-items: center;
    border-radius: 15px;
  }
.c-book-club-header__body-block > * {
      flex-grow: 1;
    }
.c-book-club-header__title {
    max-inline-size: 350px;
    display: flex;
    gap: clamp(11px, calc(11px + 11 * (100vw - 500px) / 254), 22px);
    align-items: flex-end;
  }
.c-book-club-header__title > * {
      flex-basis: 0;
    }
.c-book-club-header__verso-logo {
    flex-grow: 1;
    block-size: auto;
  }
.c-book-club-header__book-club-logo {
    flex-grow: 2;
    block-size: auto;
  }
.c-book-club-header__image {
    flex-grow: 1.85;
  }
.c-book-club-header__intro {
    flex-basis: 45%;
  }
.c-book-club-header__cta {
    flex-basis: 300px;
  }
.c-book-club-header__existing-customer-prompt {
    position: absolute;
    inset-inline-end: var(--container-padding-inline);
    inset-block-start: 15px;
    max-inline-size: 17.188rem;
    font-size: 0.75rem;
  }
.c-book-club-header__existing-customer-prompt [href] {
      text-decoration-line: underline;
    }
.c-book-club-current-selections--has-selections {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: clamp(30px, calc(30px + 10 * (100vw - 500px) / 254), 40px);
  }
.c-book-club-current-selections__selections-block {
    flex: 2.25 1 50%;
    border-radius: 15px;
  }
.c-book-club-current-selections__instructions-block {
    min-inline-size: 250px;
    flex: 1 2 25%;
    display: flex;
    flex-direction: column;
    container-type: inline-size;
    gap: clamp(14px, calc(14px + 6 * (100vw - 500px) / 254), 20px);
    font-size: clamp(1.125rem, 0.141rem + 3.1496vw, 1.625rem);
  }
.c-book-club-current-selections__instructions {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
@supports (container-type: inline-size) {
      @container (min-inline-size: 62rem) {
.c-book-club-current-selections__instructions {
        flex-direction: row
  }
      }
    }
.c-book-club-subscription-list {
  --subscription-card-border-width: 7px;
  --grid-item-min-inline-size: clamp(15.625rem, 0.898rem + 31.2500vw, 20.625rem);

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(var(--grid-item-min-inline-size), 1fr));
  align-items: flex-end;
  gap: 8px;
}
.c-book-club-subscription-card {
  --padding-block: 1rem;
  --padding-inline-start: min(12%, 2.5em);
  --padding-inline-end: calc(0.5 * var(--padding-inline-start));

  border-block-start: var(--subscription-card-border-width) solid var(--accent-color);
  border-radius: 5px;
  overflow: hidden;
}
.c-book-club-subscription-card__header {
    padding-inline: var(--padding-inline-start);
    padding-block: 0.875rem;
  }
.c-book-club-subscription-card__title {
    font-size: clamp(1.5rem, 0.027rem + 3.1250vw, 2rem);
    font-weight: bold;
    color: var(--accent-color);
  }
.c-book-club-subscription-card__description li {
      position: relative;
      display: block;
      padding: var(--padding-block) var(--padding-inline-end)
        var(--padding-block) var(--padding-inline-start);
    }
.c-book-club-subscription-card__description li:not(:first-child)::after {
        content: "";
        position: absolute;
        inset-inline-start: 0;
        inset-block-start: 0;
        inline-size: 100%;
        block-size: 1px;
        opacity: 0.5;
        background-color: var(--accent-color);
      }
.c-book-club-subscription-card__description li::before {
        content: "";
        position: absolute;
        inset-block-start: calc(var(--padding-block) + 0.75ex);
        inset-inline-start: calc(var(--padding-inline-start) - 1.25em);
        inline-size: 0.8em;
        block-size: 0.5em;
        opacity: 0.5;
        -webkit-mask: url("data:image/svg+xml,%3Csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 17.31836 12.08203%27%0A%3E%3Cpolygon points=%275.25 12.082 0 7.479 1.318 5.975 5.25 9.422 16 0 17.318 1.504 5.25 12.082%27 /%3E%3C/svg%3E%0A");
                mask: url("data:image/svg+xml,%3Csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 17.31836 12.08203%27%0A%3E%3Cpolygon points=%275.25 12.082 0 7.479 1.318 5.975 5.25 9.422 16 0 17.318 1.504 5.25 12.082%27 /%3E%3C/svg%3E%0A");
        background-color: var(--accent-color);
      }
.c-book-club-subscription-card__actions {
    --loading-overlay-inset-block-start: calc(50% - 10px);

    display: flex;
    flex-direction: column;
    gap: 0.86em;
    padding: 0.25em var(--padding-inline-end) 1.5em var(--padding-inline-start);
    font-size: clamp(0.75rem, 0.504rem + 0.7874vw, 0.875rem);
    line-height: 1.333;
  }
.c-book-club-subscription-card__primary-action {
    --transition-duration: 500ms;

    --outline-color: var(--accent-color);

    border: 1px solid var(--accent-color);

    transition: opacity var(--transition-duration)
    var(--transition-timing-function),
    border-radius var(--transition-duration) var(--transition-timing-function);

    outline-offset: 2px;
    inline-size: -moz-fit-content;
    inline-size: fit-content;
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    gap: 5px;
    padding: 0.8em 1.43em;
    border-width: 2px;
    border-radius: 2em;
  }
.c-book-club-subscription-card__primary-action:hover {
      --cart-text-opacity: 0;
      --cart-icon-opacity: 1;
      border-radius: 0;
    }
.c-book-club-subscription-card__primary-action > * {
      inline-size: -moz-max-content;
      inline-size: max-content;
    }
.c-book-club-subscription-card__primary-action > :first-child {
      font-size: clamp(0.75rem, -0.234rem + 3.1496vw, 1.25rem);
      font-weight: bold;
    }
.c-book-club-subscription-card__primary-action > :nth-child(2) {
      margin-inline-end: clamp(0px, calc(0px + 15 * (100vw - 500px) / 254), 15px);
    }
.c-book-club-subscription-card__primary-action > :last-child {
      flex-grow: 1;
    }
.c-book-club-subscription-card__secondary-action {
    text-decoration-line: underline;
  }
.c-book-club-subscription-card__cart-add {
    position: relative;
    color: var(--color);
    font-weight: bold;
  }
.c-book-club-subscription-card__cart-add > * {
      transition: opacity var(--transition-duration)
        var(--transition-timing-function);
    }
.c-book-club-subscription-card__cart-add-text {
    opacity: var(--cart-text-opacity);
  }
.c-book-club-subscription-card__cart-add-icon {
    position: absolute;
    inset-inline-end: 0;
    inset-block-start: 50%;
    transform: translateY(-50%);
    opacity: var(--cart-icon-opacity, 0);
  }
.c-book-club-collection {
  --two-up-product-grid-padding-block: 0;

  display: flex;
  flex-wrap: wrap;
  row-gap: clamp(30px, calc(30px + 45 * (100vw - 500px) / 254), 75px);
  -moz-column-gap: min(3vw, 38px);
       column-gap: min(3vw, 38px);
  padding-block-start: clamp(27.5px, calc(27.5px + 28 * (100vw - 500px) / 254), 55px);
  padding-block-end: clamp(20px, calc(20px + 10 * (100vw - 500px) / 254), 30px);
}
.c-book-club-collection__header {
    position: relative;
    min-inline-size: 20rem;
    flex: 1 2 25%;
  }
.c-book-club-collection__header p:not(:first-child) {
      margin-block-start: 1em;
    }
.c-book-club-collection__subscription-card {
    position: sticky;
    inset-block-start: calc(var(--announcement-bar-height, 0px) + var(--header-height, 0px));
  }
.c-book-club-collection__subscription-card:not(:first-child) {
      margin-block-start: clamp(20px, calc(20px + 40 * (100vw - 500px) / 254), 60px);
    }
.c-book-club-collection__body {
    flex: 2 1 50%;
    display: flex;
    flex-direction: column;
    gap: clamp(45px, calc(45px + 40 * (100vw - 500px) / 254), 85px);
  }
.c-book-club-collection__body > :first-child {
      flex-grow: 1;
    }
.c-book-club-collection__title {
    font-size: clamp(1.5rem, 0.516rem + 3.1496vw, 2rem);
    font-weight: bold;
  }
.c-book-club-collection__disclosure {
    border-block-start: 1px solid var(--border-color);
  }
.c-book-club-collection__disclosure[open] {
      --toggle-transform: rotate(180deg);
      --body-grid-template-rows: 1fr;
    }
.c-book-club-collection__disclosure-toggle {
    display: flex;
    align-items: center;
    gap: 0.8em;
    padding-block: 0.5em;
    font-size: clamp(0.875rem, -0.109rem + 3.1496vw, 1.375rem);
  }
.c-book-club-collection__disclosure-toggle h3 {
      font: inherit;
      font-weight: bold;
    }
.c-book-club-collection__disclosure-toggle-icon {
    transform: var(--toggle-transform);
  }
.c-book-club-collection__disclosure-body {
    padding-block: clamp(30px, calc(30px + 10 * (100vw - 500px) / 254), 40px);
  }
.c-book-club-footer {
  display: flex;
  flex-direction: column;
  gap: clamp(30px, calc(30px + 20 * (100vw - 500px) / 254), 50px);
  padding-block-start: clamp(45px, calc(45px + 35 * (100vw - 500px) / 254), 80px);
  font-size: clamp(0.875rem, -0.601rem + 4.7244vw, 1.625rem);
}
@media all and (min-width: 63.1875rem) {
.c-book-club-footer {
    --logo-transform: translateY(-0.25em);

    flex-direction: row-reverse;
    align-items: flex-end
}
.c-book-club-footer__text-block {
      flex-grow: 1;
      flex-basis: 41.563rem
  }
  }
.c-book-club-footer__text-block > * + * {
      margin-block-start: 0.75rem;
    }
.c-book-club-footer__logo-block {
    flex-grow: 666;
    flex-basis: 0;
    min-inline-size: 45%;
  }
.c-book-club-footer__logo {
    inline-size: clamp(78px, calc(78px + 67 * (100vw - 500px) / 254), 145px);
    block-size: clamp(61px, calc(61px + 51 * (100vw - 500px) / 254), 112.72px);
    color: var(--accent-color);
    transform: var(--logo-transform);
  }
.c-book-club-footer__heading {
    color: var(--accent-color);
    font-size: clamp(1.5rem, -0.222rem + 5.5118vw, 2.375rem);
  }
.c-book-club-footer__text {
    --accent-color: currentcolor;
  }

